body {
  font-family: Open Sans, sans-serif;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 448px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 3;
  background-color: #171a24b3;
  transition: opacity .2s linear;
  position: fixed;
  top: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.btn {
  letter-spacing: .09em;
  text-transform: uppercase;
  min-width: 160px;
  height: 40px;
  border: 1px solid var(--accent-color);
  cursor: pointer;
  background-color: #0000;
  border-radius: 25px;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.33333;
  transition-duration: .25s;
  top: 36px;
}

.btn:hover, .btn:focus {
  color: var(--header-text-color);
  background-color: var(--accent-color);
}

.btn-light {
  color: var(--header-text-color);
}

.btn-dark {
  color: var(--third-text-color);
}

.modal {
  width: 500px;
  min-height: 600px;
  background-color: var(--main-title-color);
  border-radius: 10px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .modal {
    min-width: 528px;
    min-height: 581px;
  }
}

.btn-close {
  width: 30px;
  height: 30px;
  border: 1px solid var(--fifth-text-color);
  cursor: pointer;
  background-color: #0000;
  border-radius: 50%;
  align-items: center;
  transition-duration: .25s;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.btn-close:focus, .btn-close:hover {
  fill: var(--accent-color);
  border-color: var(--accent-color);
}

.modal__title {
  text-align: center;
  color: var(--second-title-color);
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.3;
}

.modal__form-fied {
  margin-bottom: 10px;
}

.modal__form-text {
  color: var(--fifth-text-color);
  margin-bottom: 5px;
  font-size: 12px;
  display: block;
}

.modal__input-wrapper {
  display: block;
  position: relative;
}

.modal__form-input {
  width: 100%;
  height: 45px;
  border: 1px solid var(--fifth-text-color);
  border-radius: 5px;
  outline: none;
  padding-left: 40px;
  transition-duration: .25s;
}

.modal__form-input:focus {
  border-color: var(--accent-color);
}

.modal__form-input:focus + .form-icon {
  fill: var(--accent-color);
}

.form-icon {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}

.form-comment {
  width: 100%;
  height: 120px;
  border: 1px solid var(--fifth-text-color);
  resize: none;
  border-radius: 5px;
  padding: 10px;
}

.form-comment::placeholder {
  font-size: 12px;
}

.form__checkbox:checked + .form__check-text > .form__check-item {
  background-color: var(--accent-color);
  border-color: #0000;
}

.form__check-text {
  color: var(--fifth-text-color);
  margin-bottom: 20px;
  font-size: 14px;
  display: flex;
  position: relative;
}

.form__check-item {
  width: 15px;
  height: 15px;
  border: 2px solid var(--third-text-color);
  border-radius: 3px;
  justify-content: center;
  margin-right: 5px;
}

.form-btn {
  margin: 0 auto;
  display: block;
}

.contract {
  border-bottom: 1px solid var(--accent-color);
  color: var(--accent-color);
  margin-left: 9px;
  display: inline-block;
}

.pre-title_light {
  color: var(--first-text-color);
}

.pre-title_light:before {
  content: "";
  width: 63px;
  height: 1px;
  background-color: var(--first-text-color);
  margin-right: 21px;
}

.pre-title_dark {
  color: var(--third-text-color);
}

.pre-title_dark:before {
  content: "";
  width: 63px;
  height: 1px;
  background-color: var(--third-text-color);
  margin-right: 21px;
}

.section {
  padding: 84px 30px;
}

@media screen and (min-width: 768px) {
