.masters {
  background-color: var(--background-masters);
}

.masters__pre-text {
  margin-bottom: 20px;

  font-weight: 600;
  font-size: 11px;
  line-height: calc(15 / 11);
  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: var(--third-text-color);
  @media screen and (min-width: 768px) {
    width: 450px;
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: 1200px) {
    margin-left: 0;
    margin-right: 0;
  }
}

.masters__title {
  margin-bottom: 44px;

  font-family: Raleway;
  font-weight: bold;
  font-size: 42px;
  line-height: calc(49 / 42);
  letter-spacing: 0.05em;

  color: var(--third-text-color);
  @media screen and (min-width: 768px) {
    width: 450px;
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: 1200px) {
    margin-left: 0;
    margin-right: 0;
  }
}

.masters__list {
  @media screen and (min-width: 768px) {
    margin-bottom: -30px;
  }
  @media screen and (min-width: 1200px) {
    display: flex;
    margin-left: -30px;
    margin-bottom: -30px;
  }
}

.masters__item {
  margin-bottom: 30px;
  &:last-child {
    margin-bottom: 0;
  }
  @media screen and (min-width: 768px) {
    margin-left: auto;
    margin-right: auto;
    max-width: 450px;
  }
  @media screen and (min-width: 1200px) {
    flex-basis: calc((100% - 30px * 3) / 3);
    margin-left: 30px;
    max-width: 370px;
  }
}

.masters__item-title {
  margin-bottom: 8px;

  font-weight: 400;
  font-size: 18px;
  line-height: calc(25 / 18);
  text-align: center;
  letter-spacing: 0.02em;

  color: var(--third-text-color);
}

.masters__item-text {
  margin-bottom: 30px;

  font-size: 14px;
  line-height: calc(19 / 14);
  text-align: center;
  letter-spacing: 0.02em;

  color: var(--accent-color);
}

.masters__item-wrapper {
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}

.masters__top-text {
  line-height: 1.5;
  background-color: var(--accent-color);
  color: var(--main-title-color);
  position: absolute;
  top: 0;
  height: 100%;
  padding: 40px;
  transform: translate(100%, 100%);
  transition: transform 500ms linear;
  .masters__item:hover & {
    transform: translateY(0);
  }
}
